import React, { forwardRef } from 'react';
import { bool, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-disclosure-content';
const CSS_STATE = 'vds-state';

const DisclosureContent = forwardRef(({ children, className, expanded, focus, id, index, ...remainingProps }, ref) => (
  <div
    aria-hidden={expanded ? 'false' : 'true'}
    ref={ref}
    className={classnames(
      CSS_PREFIX,
      {
        [`${CSS_STATE}--hidden`]: Boolean(expanded) === false
      },
      className
    )}
    id={`disclosure-content-${id}`}
    {...remainingProps}
  >
    {children}
  </div>
));

DisclosureContent.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Expanded
   */
  expanded: bool,

  /**
   * Focus
   */
  focus: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Index
   */
  index: number
};

DisclosureContent.displayName = 'DisclosureContent';

export default DisclosureContent;
