import React, { forwardRef, useEffect, useRef, useState } from 'react';
import { bool, func, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import Icon from '../icon';

const CSS_PREFIX = 'vds-disclosure-header';
const CSS_STATE = 'vds-state';

const DisclosureHeader = forwardRef(
  (
    {
      children,
      className,
      expanded,
      focus,
      iconCollapse,
      iconExpand,
      id,
      index,
      onBlur,
      onClick,
      onFocus,
      onKeyDown,
      ...remainingProps
    },
    ref
  ) => {
    const [focused, setFocused] = useState(focus);

    ref = ref || useRef(null);

    useEffect(() => {
      if (focus && ref.current) {
        ref.current.focus();
      }
      setFocused(focus);
    }, [focus, ref]);

    const handleFocus = event => {
      setFocused(true);
      if (typeof onFocus === 'function') {
        onFocus(event);
      }
    };

    const handleBlur = event => {
      setFocused(false);
      if (typeof onBlur === 'function') {
        onBlur(event);
      }
    };

    const handleClick = (event, id) => {
      if (typeof onClick === 'function') {
        onClick(event, id);
      }
    };

    const handleKeyDown = (event, id) => {
      if (typeof onKeyDown === 'function') {
        onKeyDown(event, id);
      }
    };

    return (
      <button
        aria-controls={`disclosure-body-${id}`}
        aria-expanded={expanded ? 'true' : 'false'}
        className={classnames(
          CSS_PREFIX,
          {
            [`${CSS_STATE}--active`]: Boolean(expanded),
            [`${CSS_STATE}--focus`]: Boolean(focused),
            'focus-visible': Boolean(focused)
          },
          className
        )}
        id={`disclosure-header-${id}`}
        onBlur={handleBlur}
        onClick={e => handleClick(e, index)}
        onFocus={handleFocus}
        onKeyDown={e => handleKeyDown(e, index)}
        ref={ref}
        {...remainingProps}
      >
        <span className={`${CSS_PREFIX}-icon`}>{expanded ? iconCollapse : iconExpand}</span>
        {children}
      </button>
    );
  }
);

DisclosureHeader.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Expanded
   */
  expanded: bool,

  /**
   * Focus
   */
  focus: bool,

  /**
   * Icon collapsed
   */
  iconCollapse: node,

  /**
   * Icon expanded
   */
  iconExpand: node,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Index
   */
  index: number,

  /**
   * Callback fired when the input element loses focus.
   */
  onBlur: func,

  /**
   * Callback fired when the input element is clicked on
   */
  onClick: func,

  /**
   * Callback fired when the input element is focused on.
   */
  onFocus: func,

  /**
   * Callback fired when the input element is clicked on
   */
  onKeyDown: func
};

DisclosureHeader.defaultProps = {
  iconCollapse: <Icon name="disclosure-collapse" resolution="tiny" />,
  iconExpand: <Icon name="disclosure-expand" resolution="tiny" />
};

DisclosureHeader.displayName = 'DisclosureHeader';

export default DisclosureHeader;
