import React, { Children, cloneElement, forwardRef, useRef } from 'react';
import { array, bool, func, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import usePanelExpand from '../use-panel-expand';

const CSS_PREFIX = 'vds-disclosure';

const Disclosure = forwardRef(
  (
    {
      allowMultipleExpanded,
      children,
      className,
      defaultExpandedId,
      id = shortid.generate(),
      onChange,
      ...remainingProps
    },
    ref
  ) => {
    const uniqueId = useRef(id);
    const uid = uniqueId.current;
    const { focusedId, expandedId, updateFocusedId, updateExpandedId } = usePanelExpand(
      defaultExpandedId,
      onChange,
      allowMultipleExpanded
    );

    return (
      <div className={classnames(CSS_PREFIX, className)} id={uid} ref={ref} {...remainingProps}>
        {Children.map(children, (child, k) =>
          cloneElement(child, {
            expanded: expandedId.includes(k),
            focus: k === focusedId,
            id: `${k}--${uid}`,
            index: k,
            onClick: (e, index) => updateExpandedId(e, index),
            onKeyDown: (e, index) => updateFocusedId(e, index, Children.count(children))
          })
        )}
      </div>
    );
  }
);

Disclosure.propTypes = {
  /**
   * Allow multiple expanded
   */
  allowMultipleExpanded: bool,

  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Array of panel indexes to initially show as expanded
   */
  defaultExpandedId: array,

  /**
   * disabled
   */
  disabled: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * On Change fn
   */
  onChange: func
};

Disclosure.defaultProps = {
  defaultExpandedId: [],
  allowMultipleExpanded: true
};

Disclosure.displayName = 'Disclosure';

export default Disclosure;
