import React, { Children, cloneElement } from 'react';
import { bool, func, node, number, oneOfType, string } from 'prop-types';

const CSS_PREFIX = 'vds-disclosure-item';

const DisclosureItem = ({ children, expanded, focus, id, index, onClick, onKeyDown }) => (
  <div className={CSS_PREFIX}>
    {Children.map(children, child => cloneElement(child, { expanded, focus, id, index, onClick, onKeyDown }))}
  </div>
);

DisclosureItem.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * Expanded
   */
  expanded: bool,

  /**
   * Focus
   */
  focus: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Index
   */
  index: number,

  /**
   * on Click
   */
  onClick: func,

  /**
   * on KeyDown
   */
  onKeyDown: func
};

DisclosureItem.displayName = 'DisclosureItem';

export default DisclosureItem;
