import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-disclosure-header-wrapper';

const DisclosureHeaderWrapper = ({ children, className, ...remainingProps }) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

DisclosureHeaderWrapper.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

DisclosureHeaderWrapper.displayName = 'DisclosureHeaderWrapper';

export default DisclosureHeaderWrapper;
